import "./analytic.css";

import ChartBarHorizontally from "../chart-bar-horizontally/Chart-bar-horizontally";
import ChartPie from "../chart-pie/Chart-pie";

import { Tab, Tabs, TabList, TabPanel } from "react-tabs";

const Analytic = (probs) => {
	const workoutTabs = probs.workoutPlan.workouts.map((workout) => (
		<Tab key={workout.id}>{workout.name}</Tab>
	));

	const workoutPanels = probs.workoutPlan.workouts.map((workout) => (
		<TabPanel key={workout.id}>
			<ChartBarHorizontally
				data={probs.workoutPlan.calculateSetsPerMuscleGroup([workout.id])}
			/>
		</TabPanel>
	));

	return (
		<div className="analitics">
			<Tabs>
				<TabList>
					<h1 className="captionDiagram">Sets per Muscle</h1>
					<div className="workoutTabs">
						<Tab>Total</Tab>
						{workoutTabs}
					</div>
				</TabList>
				<TabPanel>
					<ChartBarHorizontally
						data={probs.workoutPlan.calculateSetsPerMuscleGroup()}
						desiredSets={probs.desiredSets}
					/>
				</TabPanel>
				{workoutPanels}
			</Tabs>
			<div>
				<h1 className="caption">Intesity</h1>
				<ChartPie data={probs.workoutPlan.calculateVolumeDistribution()} />
			</div>
		</div>
	);
};

export default Analytic;
