import "./exercise-info.css";

const ExerciseInfo = (props) => {
	const info = props.info;

	const dotClassName = info.isIdeal
		? "exerciseInfoDotIdeal"
		: "exerciseInfoDotNotIdeal";

	return (
		<div className="exerciseInfo">
			<span className={dotClassName}>&#9679;</span>
			<span className="exerciseInfoDescription">{info.description}</span>
		</div>
	);
};

export default ExerciseInfo;
