import React, { PureComponent } from "react";
import { PieChart, Pie, Cell, ResponsiveContainer, Legend } from "recharts";

const COLORS = ["#82CDFF", "#4089B3", "#6493B3", "#d3d3d3"];
const RADIAN = Math.PI / 180;

const renderCustomizedLabelInner = ({
	cx,
	cy,
	midAngle,
	innerRadius,
	outerRadius,
	percent,
	label,
}) => {
	//If one item is 100% do not render label and set the 100% into the center
	if (percent === 0) return;
	let x, y, textAnchor;

	if (percent !== 1) {
		const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
		x = cx + radius * Math.cos(-midAngle * RADIAN);
		y = cy + radius * Math.sin(-midAngle * RADIAN);
		textAnchor = x > cx ? "start" : "end";
	} else {
		x = cx;
		y = cy;
		textAnchor = "middle";
	}

	return (
		<text
			x={x}
			y={y}
			fill="white"
			textAnchor={textAnchor}
			dominantBaseline="central"
		>
			{`${(percent * 100).toFixed(0)}%`}
		</text>
	);
};

const renderCustomizedLabelOuter = ({
	x,
	y,
	textAnchor,
	dominantBaseline,
	percent,
	label,
}) => {
	return (
		<text
			x={x}
			y={y}
			fill="white"
			textAnchor={textAnchor}
			dominantBaseline={dominantBaseline}
		>
			{`${(percent * 100).toFixed(0)}%`}
		</text>
	);
};

export default class Example extends PureComponent {
	static demoUrl =
		"https://codesandbox.io/s/pie-chart-with-customized-label-dlhhj";

	render() {
		return (
			<ResponsiveContainer width="100%" height={275}>
				<PieChart width={400} height={400}>
					<Pie
						data={this.props.data}
						cx="50%"
						cy="50%"
						labelLine={false}
						label={renderCustomizedLabelInner}
						outerRadius={60}
						dataKey="value"
						isAnimationActive={false}
					>
						{this.props.data.map((entry, index) => (
							<Cell
								key={`cell-${index}`}
								fill={COLORS[index % COLORS.length]}
								stroke={COLORS[index % COLORS.length]}
							/>
						))}
					</Pie>
					<Pie
						data={[
							{
								name: "1-6 Reps",
								value: 15,
							},
							{
								name: "6-12 Reps",
								value: 70,
							},
							{
								name: "12-20 Reps",
								value: 15,
							},
						]}
						cx="50%"
						cy="50%"
						innerRadius={70}
						outerRadius={90}
						dataKey="value"
						isAnimationActive={false}
						legendType="none"
						label={renderCustomizedLabelOuter}
					>
						{this.props.data.map((entry, index) => (
							<Cell
								key={`cell-${index}`}
								fill={COLORS[index % COLORS.length]}
								stroke={COLORS[index % COLORS.length]}
							/>
						))}
					</Pie>
					<Legend align="center" verticalAlign="bottom" />
				</PieChart>
			</ResponsiveContainer>
		);
	}
}
