import { useState } from "react";
import Button from "../button/Button";
import "./button-custom-musclegroups.css";

const ButtonCustomMusclegroups = (props) => {
	const [hover, setHover] = useState(false);

	const coloryPrim = "#5FCBFF";
	const colorySec = "#4089B3";

	const isSelectedMuscleGroup = props.selected(props.name);
	let bgColor = "";
	switch (isSelectedMuscleGroup) {
		case "Prim":
			bgColor = coloryPrim;
			break;
		case "Sec":
			bgColor = colorySec;
			break;
		default:
			bgColor = "grey";
			break;
	}

	function addCustomMuscle(e) {
		props.onChange({
			name: props.name,
			value: e.target.name,
		});
	}

	return (
		<div
			className="buttonlayers"
			onMouseEnter={() => setHover(true)}
			onMouseLeave={() => setHover(false)}
		>
			{(!hover || !props.allowEdit || !props.parentHoverOK) && (
				<Button name={props.name} style={{ backgroundColor: bgColor }} />
			)}
			{props.parentHoverOK &&
				hover &&
				props.allowEdit &&
				!isSelectedMuscleGroup && (
					<>
						<Button
							name={"Prim"}
							onClick={addCustomMuscle}
							style={{
								borderRadius: "8px 0px 0px 8px",
								backgroundColor: coloryPrim,
							}}
						/>
						<Button
							name={"Sec"}
							onClick={addCustomMuscle}
							style={{
								borderRadius: "0px 8px 8px 0px",
								backgroundColor: colorySec,
							}}
						/>
					</>
				)}
			{props.parentHoverOK && hover && isSelectedMuscleGroup && (
				<Button
					name="Remove"
					onClick={addCustomMuscle}
					style={{ backgroundColor: "white", color: "black" }}
				/>
			)}
		</div>
	);
};

export default ButtonCustomMusclegroups;
