import "./dataEditer.css";
import { useContext, useState, Fragment } from "react";
import { WorkoutBuilderDispatch } from "../workout-builder/Workout-builder";
import {
	EXERCISE_TYPES,
	PROGRESSIONS,
	MOVEMENT_PATTERNS,
	MUSCLE_GROUPS,
} from "../../workout-plan/workout-plan-data";
import Select from "../select/Select";
import Input from "../input/Input";
import Button from "../button/Button";
import ButtonCustomMusclegroups from "../button-custom-musclegroups/Button-custom-musclegroups";
import { WorkoutPlan } from "../../workout-plan/workout-plan-object";

import CloseIcon from "@mui/icons-material/Close";

const DataEditer = (props) => {
	const dispatch = useContext(WorkoutBuilderDispatch);
	let caption = "";
	let initData = {};
	let showAll = false;

	//Initialize data depending if Updating or Creating
	switch (props.action) {
		case "CREATE_EXERCISE":
			caption = "Add";
			initData = {
				name: "",
				exerciseTypeId: "default",
				progressionId: "default",
				movementPatternId: "default",
				repsMin: "",
				rpeMin: "",
				sets: "",
				muscleGroupPrimary: [],
				muscleGroupSecondary: [],
				volumeReccomondation: "Please choose a Type first",
				progressionReccomondation: "Please choose a Exercise Type first",
			};
			break;
		case "UPDATE_EXERCISE":
			caption = "Update";
			showAll = true;
			const initValues = props.initData.exercise;
			const exTyp = EXERCISE_TYPES.find(
				(e) => e.id === initValues.exerciseType.id
			);

			let progRec;
			if (exTyp.name.includes("Compound"))
				progRec = props.progressionRec.compound;
			if (exTyp.name.includes("Isolation"))
				progRec = props.progressionRec.isolation;

			initData = {
				name: initValues.name,
				exerciseTypeId: initValues.exerciseType.id,
				progressionId: initValues.progression.id,
				movementPatternId: initValues.movementPattern.id,
				repsMin: initValues.repsMin,
				rpeMin: initValues.rpeMin,
				sets: initValues.sets,
				muscleGroupPrimary: initValues.movementPattern.muscleGroupPrimary,
				muscleGroupSecondary: initValues.movementPattern.muscleGroupSecondary,
				volumeReccomondation: `Reps ${exTyp.repsMin}-${exTyp.repsMax} RPE ${exTyp.rpeMin}-${exTyp.rpeMax}`,
				progressionReccomondation: progRec,
			};
			break;
		default:
			break;
	}

	//State for all inputs made by the user
	const [inputs, setInputs] = useState(initData);
	const handleChange = (e) => {
		let targValue;

		//Only allow numeric values for tel type inputs
		e.target.type === "tel"
			? (targValue = e.target.value.replace(/[^0-9.]+/g, ""))
			: (targValue = e.target.value);

		setInputs((prevState) => ({
			...prevState,
			[e.target.name]: targValue,
		}));
	};

	//State for muscle groups that can be changed by the user
	const [muscleGroups, setMuscleGroups] = useState({
		muscleGroupPrimary: initData.muscleGroupPrimary,
		muscleGroupSecondary: initData.muscleGroupSecondary,
	});
	const editMuscleGroups = (muscleGroup) => {
		switch (muscleGroup.value) {
			case "Remove":
				setMuscleGroups((prevState) => {
					const muscleGroupPrimary = prevState.muscleGroupPrimary.filter(
						(e) => e !== muscleGroup.name
					);
					const muscleGroupSecondary = prevState.muscleGroupSecondary.filter(
						(e) => e !== muscleGroup.name
					);
					return { muscleGroupPrimary, muscleGroupSecondary };
				});
				break;
			case "Prim":
				setMuscleGroups((prevState) => {
					if (prevState.muscleGroupPrimary.indexOf(muscleGroup.name) === -1)
						prevState.muscleGroupPrimary.push(muscleGroup.name);
					return { ...prevState };
				});
				break;
			case "Sec":
				setMuscleGroups((prevState) => {
					if (prevState.muscleGroupSecondary.indexOf(muscleGroup.name) === -1)
						prevState.muscleGroupSecondary.push(muscleGroup.name);
					return { ...prevState };
				});
				break;
			default:
				break;
		}
	};
	const initMuscleGroups = (e) => {
		const movementPatternId = e.target.value;
		const muscleGroups =
			WorkoutPlan.getMuscleGroupsFromMovementPattern(movementPatternId);
		let prim = [...muscleGroups.muscleGroupPrimary];
		let sec = [...muscleGroups.muscleGroupSecondary];
		setMuscleGroups({ muscleGroupPrimary: prim, muscleGroupSecondary: sec });
	};
	const isMuscleGroupUsed = (muscleGroupName) => {
		if (muscleGroups.muscleGroupPrimary.indexOf(muscleGroupName) !== -1)
			return "Prim";
		if (muscleGroups.muscleGroupSecondary.indexOf(muscleGroupName) !== -1)
			return "Sec";
		return "";
	};
	const [muscleGroupsHover, setMuscleGroupsHover] = useState();
	const checkMuscleGroupsHover = (e) => {
		console.log(e.target.attributes.class.nodeValue);
		if (e.target.attributes.class === "customButton")
			setMuscleGroupsHover(false);
		else setMuscleGroupsHover(true);
	};
	const muscleGroupsButtons = MUSCLE_GROUPS.map((musclegroup) => (
		<ButtonCustomMusclegroups
			key={musclegroup}
			name={musclegroup}
			allowEdit={inputs.movementPatternId !== "default"}
			onChange={editMuscleGroups}
			selected={isMuscleGroupUsed}
			parentHoverOK={muscleGroupsHover}
		/>
	));

	//State to show volume reccomondations
	const [volumeReccomondation, setVolumeReccomondation] = useState(
		initData.volumeReccomondation
	);
	//State to show progression reccomondations
	const [progressionReccomondation, setProgressionReccomondation] = useState(
		initData.progressionReccomondation
	);
	const handleReccomondations = (e) => {
		const exTyp = EXERCISE_TYPES.find((et) => et.id === e.target.value);

		let progRec;
		if (exTyp.name.includes("Compound"))
			progRec = props.progressionRec.compound;
		if (exTyp.name.includes("Isolation"))
			progRec = props.progressionRec.isolation;

		setVolumeReccomondation(
			`Reps ${exTyp.repsMin}-${exTyp.repsMax} @ RPE ${exTyp.rpeMin}-${exTyp.rpeMax}`
		);
		setProgressionReccomondation(progRec);
	};

	//State to render DataEditerScreen dynamically one by one and validate data by the go
	const [step, setStep] = useState(0);
	const validate = () => {
		let errorMsg = "";

		if (showAll || step === 0) {
			if (!inputs.name) errorMsg += "Exercise Name, ";
		}
		if (showAll || step === 1) {
			if (inputs.exerciseTypeId === "default") errorMsg += "Exercise Type, ";
			if (!inputs.sets) errorMsg += "Sets, ";
			if (!inputs.repsMin) errorMsg += "Reps, ";
			if (!inputs.rpeMin) errorMsg += "RPE, ";
		}
		if (showAll || step === 2) {
			if (inputs.movementPatternId === "default")
				errorMsg += "Movement Pattern, ";
			if (
				!muscleGroups.muscleGroupPrimary.length &&
				!muscleGroups.muscleGroupSecondary.length
			)
				errorMsg += "Muscle Group, ";
		}
		if (showAll || step === 3) {
			if (inputs.progressionId === "default") errorMsg += "Progression, ";
		}

		console.log("ALL: ", showAll);
		if (!errorMsg) {
			setValidationError("");
			if (!showAll && step !== 3) setStep(step + 1);
			else
				dispatch({
					type: props.action,
					exercise: inputs,
					customMuscleGroups: muscleGroups,
				});
		} else {
			errorMsg = errorMsg.substring(0, errorMsg.length - 2); //Cut ; from last entry
			setValidationError(`Please set ${errorMsg}`);
		}
	};

	//State to errormessages for validator
	const [validationError, setValidationError] = useState("");

	//Content of dataEditer
	//Step 0
	const screenExerciseName = (
		<Fragment>
			<h3 name="exerciseTypes" className="dataEditerDescription">
				Exercise Name
			</h3>
			<Input
				name="name"
				type="text"
				value={inputs.name}
				onChange={handleChange}
			/>
		</Fragment>
	);
	//Step 1
	const screenExerciseType = (
		<Fragment>
			<h3 className="dataEditerDescription">Exercise Type</h3>
			<Select
				name="exerciseTypeId"
				options={EXERCISE_TYPES}
				selected={inputs.exerciseTypeId}
				onChange={(e) => {
					handleChange(e);
					handleReccomondations(e);
				}}
			/>
			{!showAll && (
				<>
					<div className="Reccomondations">
						<h3 className="ReccomondationsText">Recommendation: </h3>
						<h3 className="ReccomondationsText">{volumeReccomondation}</h3>
					</div>
				</>
			)}
			<h3 className="dataEditerDescription">Volume</h3>
			<div className="volumeData">
				<div className="volumeDataInput">
					<Input
						name="sets"
						type="tel"
						value={inputs.sets}
						onChange={handleChange}
					/>
					<span>Sets</span>
				</div>
				<span className="volumeDataInputSeperators">X</span>
				<div className="volumeDataInput">
					<Input
						name="repsMin"
						type="tel"
						value={inputs.repsMin}
						onChange={handleChange}
					/>
					<span>Reps</span>
				</div>
				<span className="volumeDataInputSeperators">@</span>
				<div className="volumeDataInput">
					<Input
						name="rpeMin"
						type="tel"
						value={inputs.rpeMin}
						onChange={handleChange}
					/>
					<span>RPE</span>
				</div>
			</div>
		</Fragment>
	);
	//Step 2
	const screenMovementPattern = (
		<Fragment>
			<h3 className="dataEditerDescription">Movement Pattern</h3>
			<Select
				name="movementPatternId"
				options={MOVEMENT_PATTERNS}
				selected={inputs.movementPatternId}
				onChange={(e) => {
					handleChange(e);
					initMuscleGroups(e);
				}}
			/>
			<h3 className="dataEditerDescription">Muscle Groups</h3>
			<div
				className="muscleGroups"
				onMouseMove={(e) => checkMuscleGroupsHover(e)}
			>
				{muscleGroupsButtons}
			</div>
		</Fragment>
	);
	//Step 3
	const screenProgression = (
		<Fragment>
			<h3 className="dataEditerDescription">Progression</h3>
			<Select
				name="progressionId"
				options={PROGRESSIONS}
				selected={inputs.progressionId}
				onChange={handleChange}
			/>
			{!showAll && (
				<>
					<div className="Reccomondations">
						<h3 className="ReccomondationsText">Recommendation: </h3>
						<h3 className="ReccomondationsText">{progressionReccomondation}</h3>
					</div>
				</>
			)}
		</Fragment>
	);
	const buttonNext = <Button name="Next" onClick={validate} />;
	const buttonPrevisous = (
		<Button name="Back" onClick={() => setStep(step - 1)} />
	);
	const buttonComplete = <Button name={caption} onClick={validate} />;

	const validationErrorMsg = (
		<h3 className="dataEditerValidation">{`${validationError}`}</h3>
	);

	return (
		<div className="dataEditer">
			<div className="dataEditerHead">
				<h2 className="dataEditerCaption">{`${caption} Exercise`}</h2>
				<CloseIcon
					onClick={() =>
						dispatch({
							type: "CANCEL",
						})
					}
				></CloseIcon>
			</div>

			{(showAll || step === 0) && screenExerciseName}
			{(showAll || step === 1) && screenExerciseType}
			{(showAll || step === 2) && screenMovementPattern}
			{(showAll || step === 3) && screenProgression}
			<div className="dataEditerButtons">
				{!showAll && step !== 0 && buttonPrevisous}
				{!showAll && step !== 3 && buttonNext}
				{(showAll || step === 3) && buttonComplete}
			</div>
			{validationError && validationErrorMsg}
		</div>
	);
};

export default DataEditer;
