import { useState } from "react";

import WorkoutBuilder from "./components/workout-builder/Workout-builder";
import LandingPage from "./landing-page/Landing-page";
import InitChooser from "./init-chooser/Init-chooser";
import { WorkoutPlan } from "./workout-plan/workout-plan-object";

const Main = () => {
	let navStartpoint = "";
	let planStartpoint = "";

	//Load workoutplan and training age if already saved
	const wpLS = JSON.parse(localStorage.getItem("workoutPlan"));
	const ageLS = localStorage.getItem("trainingAge");

	if (!wpLS || !ageLS) {
		navStartpoint = "LandingPage";
	} else {
		let wp = new WorkoutPlan(wpLS.workouts.map((wo) => wo.name).toString());
		wp.workouts = wpLS.workouts;
		navStartpoint = "WorkoutBuilder";
		planStartpoint = wp;
	}

	const [plan, setPlan] = useState(planStartpoint);

	const setPlanInput = (e) => {
		setPlan(e.target.value);
	};

	const setPlanClick = (e) => {
		if (e.target.innerText && e.target.innerText !== "NA")
			setPlan(e.target.innerText);
	};

	const [navigator, setNavigator] = useState(navStartpoint);

	const nav = (e) => {
		switch (e.target.name) {
			case "Start Building":
				setNavigator("InitChooser");
				break;
			case "Continue":
				setNavigator("WorkoutBuilder");
				break;
			default:
				setNavigator("InitChooser");
				break;
		}
	};

	const [trainingAge, setTrainingAge] = useState(ageLS);

	const handleTrainingAge = (age) => {
		//Save on every change
		localStorage.setItem("trainingAge", age);
		setTrainingAge(age);
	};

	return (
		<>
			{navigator === "LandingPage" && <LandingPage nav={nav} />}
			{navigator === "InitChooser" && (
				<InitChooser
					nav={nav}
					split={plan}
					setSplitClick={setPlanClick}
					setSplitInput={setPlanInput}
					handleTrainingAge={handleTrainingAge}
				/>
			)}
			{navigator === "WorkoutBuilder" && (
				<WorkoutBuilder nav={nav} plan={plan} trainingAge={trainingAge} />
			)}
		</>
	);
};

export default Main;
