import { useState } from "react";

import "./init-chooser.css";
import Table from "../components/table/Table";
import Select from "../components/select/Select";
import Button from "../components/button/Button";
import Input from "../components/input/Input";
import {
	TRAINING_AGES,
	TRAINING_AGES_RECOMMONDATIONS,
} from "../workout-plan/workout-plan-data";

const InitChooser = (props) => {
	const [trainingAgeRec, setTrainingAgeRec] = useState("");
	const [validationError, setValidationError] = useState("");

	const handleTrainingAgeRec = (e) => {
		const ageRec = TRAINING_AGES_RECOMMONDATIONS.find(
			(rec) => rec.age === e.target.value
		);
		setTrainingAgeRec(
			"Recommended Trainings per Week: " + ageRec.weeklyFrequency
		);
	};

	const validator = (e) => {
		if (trainingAgeRec && props.split) props.nav(e);
		else
			setValidationError(
				"Please enter your training age and choose a valid workout split"
			);
	};

	return (
		<div className="initPage">
			<div className="initPageContent">
				<h1 className="landingPageCaption">
					Fit<strong>Build</strong>
				</h1>

				<div>
					<h1>Workout split</h1>
					<div className="initPageTrainingAge">
						<p>Before you choose one, please enter your training age:</p>
						<div className="initPageTrainingAgeChooser">
							<Select
								name="trainingAge"
								options={TRAINING_AGES}
								onChange={(e) => {
									props.handleTrainingAge(e.target.value);
									handleTrainingAgeRec(e);
								}}
							/>
						</div>
					</div>

					<Table setSplitClick={props.setSplitClick} />
					<p className="initTrainingAgeRec">{trainingAgeRec}</p>
				</div>

				<div className="initContinue">
					<Input
						name="split"
						type="text"
						value={props.split}
						style={{
							fontSize: "1.1em",
						}}
						onChange={props.setSplitInput}
					/>
					<Button
						name="Continue"
						style={{
							maxWidth: "250px",
							minHeight: "40px",
							fontSize: "1.35em",
						}}
						onClick={validator}
					/>
				</div>
				<p className="initTrainingAgeRec">
					Hint: If you would rather use your own split, just insert your
					training days separated by a comma
				</p>
				<p className="initValidation">{`${validationError}`}</p>
			</div>
		</div>
	);
};

export default InitChooser;
