import { useState, useRef, useContext } from "react";
import { WorkoutBuilderDispatch } from "../workout-builder/Workout-builder";
import "./exercise.css";
import ExerciseInfo from "../exercise-info/Exercise-info";
import ExerciseActions from "../exercise-actions/Exercise-actions";

import { useDrag, useDrop } from "react-dnd";

const Exercise = (props) => {
	const exercise = props.exercise;

	const [hover, setHover] = useState(false);
	const dispatch = useContext(WorkoutBuilderDispatch);

	const ref = useRef(null);

	const [{}, drop] = useDrop({
		accept: "EXERCISE",
		hover(ex, monitor) {
			if (!ref.current) {
				return;
			}
			const dragId = ex.id;
			const hoverId = exercise.id;

			// Don't replace items with themselves
			if (dragId === hoverId) {
				return;
			}
			// Determine rectangle on screen
			const hoverBoundingRect = ref.current?.getBoundingClientRect();
			// Get vertical middle
			const hoverMiddleY =
				(hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
			// Determine mouse position
			const clientOffset = monitor.getClientOffset();
			// Get pixels to the top
			const hoverClientY = clientOffset.y - hoverBoundingRect.top;
			// Only perform the move when the mouse has crossed half of the items height
			if (hoverClientY < hoverMiddleY) {
				dispatch({
					type: "MOVE_EXERCISE",
					moveId: dragId,
					insertId: hoverId,
					moveDir: "BEFORE",
				});
			}
			if (hoverClientY > hoverMiddleY) {
				dispatch({
					type: "MOVE_EXERCISE",
					moveId: dragId,
					insertId: hoverId,
					moveDir: "AFTER",
				});
			}
		},
	});

	const [{ isDragging }, drag] = useDrag({
		type: "EXERCISE",
		item: () => {
			return { id: exercise.id };
		},
		collect(monitor) {
			if (monitor.isDragging()) setHover(false);
			return { isDragging: monitor.isDragging() };
		},
		//This is tricky, the original component may be unmounted during the dragging and later resurrected with a different parent
		isDragging(monitor) {
			return exercise.id === monitor.getItem().id;
		},
	});

	drag(drop(ref));

	const exerciseVolume = {
		//IDEA: Porgramm isIdeal (not used in first release)
		isIdeal: true,
		description: `${exercise.sets} Sets x ${exercise.repsMin} Reps @ ${exercise.rpeMin} RPE`,
	};

	const exerciseProgression = {
		isIdeal: false,
		description: exercise.progression.name,
	};

	return (
		<div
			ref={ref}
			style={{
				opacity: isDragging ? 0.35 : 1,
			}}
		>
			<div
				className="exercise"
				onMouseOver={() => setHover(true)}
				onMouseOut={() => setHover(false)}
			>
				<h2 className="exercisename" ref={drag}>
					{exercise.name}
				</h2>
				<ExerciseInfo info={exerciseVolume} />
				<ExerciseInfo info={exerciseProgression} />
				{hover && <ExerciseActions exerciseId={exercise.id} dragRef={drag} />}
			</div>
		</div>
	);
};

export default Exercise;
