import React, { PureComponent } from "react";
import {
	BarChart,
	Bar,
	XAxis,
	YAxis,
	CartesianGrid,
	Legend,
	ResponsiveContainer,
	ReferenceArea,
} from "recharts";

export default class ChartBarHorizontally extends PureComponent {
	static demoUrl = "https://codesandbox.io/s/stacked-bar-chart-s47i2";

	render() {
		const desiredRange = (() => {
			if (this.props.desiredSets) {
				return this.props.desiredSets;
			} else {
				return { min: 0, max: 0 };
			}
		})();

		const maxSets = Math.max(
			...this.props.data.map(
				(muscleGroup) => muscleGroup.primary + muscleGroup.secondary
			),
			desiredRange.max + 0.5,
			5
		);

		return (
			<ResponsiveContainer width="100%" height={500}>
				<BarChart
					layout={"vertical"}
					width={500}
					height={300}
					data={this.props.data}
					margin={{
						top: 20,
						right: 30,
						left: 20,
						bottom: 5,
					}}
				>
					<CartesianGrid strokeDasharray="3 3" />
					<XAxis
						type="number"
						domain={[0, maxSets % 2 === 0 ? maxSets + 2 : maxSets + 1]} // This ensures that the description is without holes and no even numbers appear
						tickCount={maxSets}
						interval={0}
						allowDecimals={false}
					/>
					<YAxis type="category" dataKey="name" />
					<Legend />
					<Bar
						name="Primary Muscle Group"
						dataKey="primary"
						stackId="a"
						fill="#5FCBFF"
					/>
					<Bar
						name="Secondary Muscle Group"
						dataKey="secondary"
						stackId="a"
						fill="#4089B3"
					/>
					{this.props.desiredSets && (
						<Bar name="Desired Sets" dataKey="" stackId="a" fill="lightgrey" />
					)}

					<ReferenceArea
						x1={desiredRange.min}
						x2={desiredRange.max}
						y1={this.props.data[0].name}
						y2={this.props.data[this.props.data.length - 1].name}
						fill="lightgrey"
						fillOpacity={0.3}
						stroke="white"
						strokeOpacity={0.2}
						isFront={false}
					/>
				</BarChart>
			</ResponsiveContainer>
		);
	}
}
