import { WorkoutBuilderDispatch } from "../workout-builder/Workout-builder";
import React, { useContext, useRef } from "react";
import "./sidebar.css";
import img from "./resources/icon.png";

import Tippy from "@tippyjs/react";
import "tippy.js/dist/tippy.css";
import DownloadIcon from "@mui/icons-material/Download";
import UploadIcon from "@mui/icons-material/Upload";
import SyncIcon from "@mui/icons-material/Sync";
import LibraryBooksIcon from "@mui/icons-material/LibraryBooks";
import InfoIcon from "@mui/icons-material/Info";

function Sidebar(probs) {
	const dispatch = useContext(WorkoutBuilderDispatch);
	const inputFileRef = useRef(null);

	const onUploadInputFile = (e) => {
		const reader = new FileReader();
		reader.onload = (evt) => {
			console.log(JSON.parse(evt.target.result));
			dispatch({
				type: "REINIT_WORKOUTS",
				workouts: JSON.parse(evt.target.result).workouts,
			});
		};
		reader.readAsText(e.target.files[0]);
	};
	const uploadInputFile = () => {
		inputFileRef.current.click();
	};
	return (
		<div className="sidebar">
			<div className="sidebar_actions">
				<Tippy content="FitBuild">
					<img className="sidebar_icon" src={img} alt={""} />
				</Tippy>
				<Tippy content="Comming soon: Large dataset of predefined exercises">
					<LibraryBooksIcon fontSize="large"></LibraryBooksIcon>
				</Tippy>
				<Tippy content="Comming soon: Instructions to assist you during the building process">
					<InfoIcon fontSize="large"></InfoIcon>
				</Tippy>
				<Tippy content="Save workoutplan to file">
					<DownloadIcon
						fontSize="large"
						onClick={() => {
							const FileSaver = require("file-saver");
							const blob = new Blob([JSON.stringify(probs.workoutPlan)], {
								type: "text/plain;charset=utf-8",
							});
							FileSaver.saveAs(blob, "fitbuild_workoutplan.json");
						}}
					></DownloadIcon>
				</Tippy>
				<Tippy content="Load workoutplan from file">
					<UploadIcon fontSize="large" onClick={uploadInputFile}></UploadIcon>
				</Tippy>
				<input
					type="file"
					className="inputFile"
					ref={inputFileRef}
					onChange={onUploadInputFile}
				/>
			</div>

			<div className="sidebar_actions">
				<Tippy content="Restart from scratch">
					<SyncIcon
						fontSize="large"
						color="warning"
						onClick={() => {
							if (window.confirm("All data will be erased. Continue?")) {
								localStorage.clear();
								window.location.reload();
							}
						}}
					></SyncIcon>
				</Tippy>
			</div>
		</div>
	);
}

export default Sidebar;
