import "./workout.css";
import { useContext } from "react";
import { WorkoutBuilderDispatch } from "../workout-builder/Workout-builder";
import Exercise from "../exercise/Exercise";
import AddIcon from "@mui/icons-material/Add";
import Tippy from "@tippyjs/react";
import "tippy.js/dist/tippy.css";
import { useDrop } from "react-dnd";

const Workout = (props) => {
	const dispatch = useContext(WorkoutBuilderDispatch);

	const [{}, drop] = useDrop({
		accept: "EXERCISE",
		hover(ex, monitor) {
			const draggedExId = ex.id;
			dispatch({
				type: "MOVE_EXERCISE",
				moveId: draggedExId,
				woId: props.workout.id,
			});
		},
	});

	const exercises = props.workout.exercises.map((exercise) => (
		<Exercise key={exercise.id} exercise={exercise} />
	));

	return (
		<div className="workout">
			<h1 className="workoutCaption">{props.workout.name}</h1>
			<Tippy content="Add exercise">
				<AddIcon
					className="workoutAdd"
					fontSize="large"
					onClick={() =>
						dispatch({
							type: "CREATE_EXERCISE_WITH_DATA_EDITER",
							workoutId: props.workout.id,
						})
					}
				/>
			</Tippy>
			{exercises}
			<div className="workoutExerciseFiller" ref={drop}></div>
		</div>
	);
};

export default Workout;
