import {
	EXERCISE_TYPES,
	PROGRESSIONS,
	MOVEMENT_PATTERNS,
	MUSCLE_GROUPS,
} from "./workout-plan-data";

//Improvement: Class should return new Object everytime something changes when user is not dragging things
export class WorkoutPlan {
	constructor(sp) {
		//Expected for split: String with ',' as the seperator
		const splitArr = sp.split(",");
		this.workouts = [];
		for (let i = 0; i <= splitArr.length - 1; i++)
			this.workouts.push(new Workout(uniqueID("WO-"), splitArr[i]));
	}

	//Improvement: Datastructure should be in a database cause the following is ugly and i nearly puked coding it, but hey... it works :D

	getExercise(exerciseId) {
		return this.exerciseSearcher(exerciseId, "EXERCISE");
	}

	getWorkoutFromExercise(exerciseId) {
		return this.exerciseSearcher(exerciseId, "WORKOUT");
	}

	exerciseSearcher(exerciseId, type = "EXERCISE") {
		//Helperfunction
		//Iterates over all exercises and returns the exercise itself, or the workout in wicht it lives
		for (let workout of this.workouts) {
			let exercise = workout.exercises.find(
				(exercise) => exercise.id === exerciseId
			);
			if (exercise) {
				switch (type) {
					case "EXERCISE":
						return exercise;
					case "WORKOUT":
						return workout;
					default:
						return {};
				}
			}
		}
		return {};
	}

	getWorkout(workoutId) {
		return this.workouts.find((wo) => wo.id === workoutId);
	}

	addExercise(workoutId, exercise, muscleGroups = null) {
		const workout = this.workouts.find((workout) => workout.id === workoutId);

		workout.exercises.push(
			new Exercise(uniqueID("EX-"), exercise, muscleGroups)
		);
	}

	updateExercise(exerciseId, exerciseUpdateData, customMuscleGroups = null) {
		const exercise = this.getExercise(exerciseId);

		exercise.name = exerciseUpdateData.name;
		exercise.sets = exerciseUpdateData.sets;
		exercise.repsMin = exerciseUpdateData.repsMin;
		exercise.repsMax = exerciseUpdateData.repsMax;
		exercise.rpeMin = exerciseUpdateData.rpeMin;
		exercise.rpeMax = exerciseUpdateData.rpeMax;
		exercise.exerciseType = EXERCISE_TYPES.find(
			(e) => e.id === exerciseUpdateData.exerciseTypeId
		);
		exercise.progression = PROGRESSIONS.find(
			(e) => e.id === exerciseUpdateData.progressionId
		);
		exercise.movementPattern = {
			...MOVEMENT_PATTERNS.find(
				(e) => e.id === exerciseUpdateData.movementPatternId
			),
		};
		if (customMuscleGroups) {
			exercise.movementPattern.muscleGroupPrimary = [
				...customMuscleGroups.muscleGroupPrimary,
			];
			exercise.movementPattern.muscleGroupSecondary = [
				...customMuscleGroups.muscleGroupSecondary,
			];
		} else {
			exercise.movementPattern.muscleGroupPrimary = [
				...exercise.movementPattern.muscleGroupPrimary,
			];
			exercise.movementPattern.muscleGroupSecondary = [
				...exercise.movementPattern.muscleGroupSecondary,
			];
		}
	}

	copyExercise(exerciseId) {
		const workout = this.getWorkoutFromExercise(exerciseId);
		const oldExercise = this.getExercise(exerciseId);

		//Bring workout to right format for class constructor
		const newExercise = {
			name: oldExercise.name,
			sets: oldExercise.sets,
			repsMin: oldExercise.repsMin,
			repsMax: oldExercise.repsMax,
			rpeMin: oldExercise.rpeMin,
			rpeMax: oldExercise.rpeMax,
			exerciseTypeId: oldExercise.exerciseType.id,
			progressionId: oldExercise.progression.id,
			movementPatternId: oldExercise.movementPattern.id,
		};

		const newCustomMuscleGroups = {
			muscleGroupPrimary: [...oldExercise.movementPattern.muscleGroupPrimary],
			muscleGroupSecondary: [
				...oldExercise.movementPattern.muscleGroupSecondary,
			],
		};

		workout.exercises.push(
			new Exercise(uniqueID("EX-"), newExercise, newCustomMuscleGroups)
		);
	}

	deleteExercise(exerciseId) {
		const workout = this.getWorkoutFromExercise(exerciseId);
		const exercise = this.getExercise(exerciseId);
		const index = workout.exercises.indexOf(exercise);

		//Removes one item from the indef if found
		if (index > -1) {
			workout.exercises.splice(index, 1);
		}
	}

	moveExercise(
		exMoveId,
		exInsertId = null,
		moveDir = null,
		emptyWorkoutToInsert = null
	) {
		//Moves a exercise before or after the insert exercise
		console.log(exMoveId, exInsertId, moveDir, emptyWorkoutToInsert);
		const woDelete = this.getWorkoutFromExercise(exMoveId);
		console.log(woDelete);
		const exMove = this.getExercise(exMoveId);
		const deleteIndex = woDelete.exercises.findIndex((ex) => ex === exMove);

		if (!emptyWorkoutToInsert) {
			const woInsert = this.getWorkoutFromExercise(exInsertId);
			const exInsert = this.getExercise(exInsertId);

			woDelete.exercises.splice(deleteIndex, 1);

			const insertIndex = woInsert.exercises.findIndex((ex) => ex === exInsert);

			switch (moveDir) {
				case "BEFORE":
					woInsert.exercises.splice(insertIndex, 0, exMove);
					break;
				case "AFTER":
					woInsert.exercises.splice(insertIndex + 1, 0, exMove);
					break;
				default:
					break;
			}
		} else {
			const woInsert = this.getWorkout(emptyWorkoutToInsert);
			woDelete.exercises.splice(deleteIndex, 1);
			woInsert.exercises.push(exMove);
		}
	}

	calculateSetsPerMuscleGroup(searchFromWorkouts = null) {
		//Returns Array of Objects for every musclegrou with the following format:
		//{
		//	name: "Pecs",
		//	primary: 4,
		//	secondary: 5,
		//}

		//Initialize array with counter variables set to 0
		let muscleGroupCounter;
		muscleGroupCounter = MUSCLE_GROUPS.map((muscleGroup) => ({
			name: muscleGroup,
			primary: 0,
			secondary: 0,
		}));

		let searchIndex = -1;

		let woSearch = [];
		if (searchFromWorkouts) {
			for (const woId of searchFromWorkouts) {
				woSearch.push(this.getWorkout(woId));
			}
		} else {
			woSearch = this.workouts;
		}
		console.log(searchFromWorkouts);
		//Extract used muscles from objcet data
		for (const workout of woSearch) {
			for (const exercise of workout.exercises) {
				for (const primaryMuscleName of exercise.movementPattern
					.muscleGroupPrimary) {
					searchIndex = muscleGroupCounter.findIndex(
						(muscleGroup) => muscleGroup.name === primaryMuscleName
					);
					if (searchIndex !== -1) {
						muscleGroupCounter[searchIndex].primary =
							muscleGroupCounter[searchIndex].primary + parseInt(exercise.sets);
					}
				}
				for (const secondaryMuscleName of exercise.movementPattern
					.muscleGroupSecondary) {
					searchIndex = muscleGroupCounter.findIndex(
						(muscleGroup) => muscleGroup.name === secondaryMuscleName
					);
					if (searchIndex !== -1) {
						muscleGroupCounter[searchIndex].secondary =
							muscleGroupCounter[searchIndex].secondary +
							parseInt(exercise.sets);
					}
				}
			}
		}

		return muscleGroupCounter;
	}

	calculateVolumeDistribution() {
		//Returns Array of Objects with the following format:

		let counter = [
			{ name: "1-6 Reps", value: 0 },
			{ name: "6-12 Reps", value: 0 },
			{ name: "12-20 Reps", value: 0 },
			{ name: "Recommendation", value: 0 },
		];

		for (const workout of this.workouts) {
			for (const exercise of workout.exercises) {
				switch (true) {
					case exercise.repsMin <= 5:
						counter[0].value = counter[0].value + parseInt(exercise.sets);
						break;
					case exercise.repsMin <= 12:
						counter[1].value = counter[1].value + parseInt(exercise.sets);
						break;
					case exercise.repsMin > 12:
						counter[2].value = counter[2].value + parseInt(exercise.sets);
						break;
					default:
						break;
				}
			}
		}
		return counter;
	}

	static getMuscleGroupsFromMovementPattern(movementPatternId) {
		const movementPattern = MOVEMENT_PATTERNS.find(
			(mp) => mp.id === movementPatternId
		);

		return {
			muscleGroupPrimary: movementPattern.muscleGroupPrimary,
			muscleGroupSecondary: movementPattern.muscleGroupSecondary,
		};
	}
}

export class Workout {
	constructor(id, name) {
		this.id = id;
		this.name = name;
		this.exercises = [];
	}
}

export class Exercise {
	constructor(id, exercise, customMuscleGroups = null) {
		this.id = id;
		this.name = exercise.name;
		this.sets = exercise.sets;
		this.repsMin = exercise.repsMin;
		this.repsMax = exercise.repsMax;
		this.rpeMin = exercise.rpeMin;
		this.rpeMax = exercise.rpeMax;
		this.exerciseType = EXERCISE_TYPES.find(
			(e) => e.id === exercise.exerciseTypeId
		);
		this.progression = PROGRESSIONS.find(
			(e) => e.id === exercise.progressionId
		);
		this.movementPattern = {
			...MOVEMENT_PATTERNS.find((e) => e.id === exercise.movementPatternId),
		};
		if (customMuscleGroups) {
			this.movementPattern.muscleGroupPrimary = [
				...customMuscleGroups.muscleGroupPrimary,
			];
			this.movementPattern.muscleGroupSecondary = [
				...customMuscleGroups.muscleGroupSecondary,
			];
		} else {
			this.movementPattern.muscleGroupPrimary = [
				...this.movementPattern.muscleGroupPrimary,
			];
			this.movementPattern.muscleGroupSecondary = [
				...this.movementPattern.muscleGroupSecondary,
			];
		}
	}
}

export class MovementPattern {
	constructor(pattern) {
		this.name = pattern.name;
		this.muscleGroupPrimary = pattern.muscleGroupPrimary;
		this.muscleGroupSecondary = pattern.muscleGroupSecondary;
		this.exercise = "";
	}
}

export class Progression {
	constructor(progression) {
		this.name = progression.name;
	}
}

const uniqueID = function (beginningID) {
	return (
		beginningID + Date.now().toString(36) + Math.random().toString(16).slice(2)
	);
};
