import "./workout-builder.css";
import React, { useReducer } from "react";

import Sidebar from "../sidebar/Sidebar";
import Workout from "../workout/Workout";
import Analytic from "../analytic/Analytic";
import OverlayDataEditer from "../overlay-data-editer/Overlay-data-editer";

import { WorkoutPlan } from "../../workout-plan/workout-plan-object";
import { TRAINING_AGES_RECOMMONDATIONS } from "../../workout-plan/workout-plan-data";

import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";

export const WorkoutBuilderDispatch = React.createContext(null);

function init(plan) {
	let wp;

	if (typeof plan === "string") {
		wp = new WorkoutPlan(plan);
	} else {
		wp = plan;
	}
	return {
		dataEditerStatus: { isActive: false, action: "", initData: {} },
		actionElementID: "",
		workoutPlan: wp,
	};
}

function cleanState(wp) {
	//Save on every change
	localStorage.setItem("workoutPlan", JSON.stringify(wp));

	return {
		workoutPlan: wp,
		dataEditerStatus: { isActive: false, action: "", initData: {} },
		actionElementID: "",
	};
}

function reducer(state, action) {
	switch (action.type) {
		case "CREATE_EXERCISE_WITH_DATA_EDITER":
			state.actionElementID = action.workoutId;
			state.dataEditerStatus = {
				isActive: true,
				action: "CREATE_EXERCISE",
				initData: {},
			};
			return { ...state };
		case "UPDATE_EXERCISE_WITH_DATA_EDITER":
			state.actionElementID = action.exerciseId;
			const exercise = state.workoutPlan.getExercise(state.actionElementID);
			state.dataEditerStatus = {
				isActive: true,
				action: "UPDATE_EXERCISE",
				initData: { exercise },
			};
			return { ...state };
		case "CREATE_EXERCISE":
			state.workoutPlan.addExercise(
				state.actionElementID,
				action.exercise,
				action.customMuscleGroups
			);
			return cleanState(state.workoutPlan);
		case "UPDATE_EXERCISE":
			state.workoutPlan.updateExercise(
				state.actionElementID,
				action.exercise,
				action.customMuscleGroups
			);
			return cleanState(state.workoutPlan);
		case "DELETE_EXERCISE":
			state.workoutPlan.deleteExercise(action.exerciseId);
			return cleanState(state.workoutPlan);
		case "COPY_EXERCISE":
			state.workoutPlan.copyExercise(action.exerciseId);
			return cleanState(state.workoutPlan);
		case "MOVE_EXERCISE":
			state.workoutPlan.moveExercise(
				action.moveId,
				action.insertId,
				action.moveDir,
				action.woId
			);
			return cleanState(state.workoutPlan);
		case "REINIT_WORKOUTS":
			state.workoutPlan.workouts = action.workouts;
			return cleanState(state.workoutPlan);
		case "CANCEL":
			return cleanState(state.workoutPlan);
		default:
			throw new Error();
	}
}

const WorkoutBuilder = (props) => {
	const [state, dispatch] = useReducer(reducer, props.plan, init);
	const workouts = state.workoutPlan.workouts.map((workout) => (
		<Workout key={workout.id} workout={workout} />
	));

	const trainingAgeRec = TRAINING_AGES_RECOMMONDATIONS.find(
		(rec) => rec.age === props.trainingAge
	);

	return (
		<DndProvider backend={HTML5Backend}>
			<WorkoutBuilderDispatch.Provider value={dispatch}>
				<div className="workoutBuilder">
					<div className="dashboard">
						<Sidebar workoutPlan={state.workoutPlan} />
						<div className="workouts">{workouts}</div>
						<Analytic
							workoutPlan={state.workoutPlan}
							desiredSets={{
								min: trainingAgeRec.setsPerMuscleMin,
								max: trainingAgeRec.setsPerMuscleMax,
							}}
						/>
					</div>

					{state.dataEditerStatus.isActive && (
						<OverlayDataEditer
							action={state.dataEditerStatus.action}
							initData={state.dataEditerStatus.initData}
							progressionRec={{
								compound: trainingAgeRec.progressionCompound,
								isolation: trainingAgeRec.progressionIsolation,
							}}
						/>
					)}
				</div>
			</WorkoutBuilderDispatch.Provider>
		</DndProvider>
	);
};

export default WorkoutBuilder;
