import "./exercise-actions.css";
import { useContext } from "react";
import { WorkoutBuilderDispatch } from "../workout-builder/Workout-builder";

import ModeEditOutlinedIcon from "@mui/icons-material/ModeEditOutlined";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import DeleteOutlineIcon from "@mui/icons-material/DeleteOutline";
import PanToolOutlinedIcon from "@mui/icons-material/PanToolOutlined";
import Tippy from "@tippyjs/react";
import "tippy.js/dist/tippy.css";

const ExerciseActions = (probs) => {
	const dispatch = useContext(WorkoutBuilderDispatch);

	return (
		<div className="exerciseActions">
			<div className="exerciseActionIcon" ref={probs.dragRef}>
				<Tippy content="Move">
					<PanToolOutlinedIcon fontSize="small" />
				</Tippy>
			</div>
			<div className="exerciseActionIcon">
				<Tippy content="Edit">
					<ModeEditOutlinedIcon
						fontSize="small"
						onClick={() =>
							dispatch({
								type: "UPDATE_EXERCISE_WITH_DATA_EDITER",
								exerciseId: probs.exerciseId,
							})
						}
					/>
				</Tippy>
			</div>
			<div className="exerciseActionIcon">
				<Tippy content="Copy">
					<ContentCopyIcon
						fontSize="small"
						onClick={() =>
							dispatch({ type: "COPY_EXERCISE", exerciseId: probs.exerciseId })
						}
					/>
				</Tippy>
			</div>
			<div className="exerciseActionIcon">
				<Tippy content="Delete">
					<DeleteOutlineIcon
						fontSize="small"
						onClick={() =>
							dispatch({
								type: "DELETE_EXERCISE",
								exerciseId: probs.exerciseId,
							})
						}
					/>
				</Tippy>
			</div>
		</div>
	);
};

export default ExerciseActions;
